<template>
    <div class="home">
        <div class="home_main">
            <div class="h_ibox">
                <img :src="require(`./img/home_1.jpg`)">
                <h1>  ,     </h1>
                <p>  (La La Land), 2016,  </p>
            </div>
        </div>
        <h1>    ? </h1> <p>{{c_text}}</p>
        <div class="h_content">
            <template v-for="color in hex_list" >
                <div :key="color" @click="asd(color.hex)" class="hex_box" v-if="color.tag.join().includes(day)">
                    <router-link 
                    router :to="{path : '/about', name : 'about', id:'11', aaaa:color.hex}">
                    </router-link>
                    <div class="hex_card">
                        <img :src="require(`./img/${color.img}`)">
                        <p>{{color.hex}}</p>
                    </div>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
import hex1 from './js/list'

export default {
    data() {
        return {
            hex_list : hex1,
            day : 'red',
            c_text : 'asd',
        }
    },

    methods: {
        asd(color) {
            this.$router.push({
                name: "about",
                params: { name : 'about', aaaa: color}
            });
        }
    },

    mounted()  {
        setTimeout(() => {
            const random = Math.floor(Math.random() * 6 + 1);
            if(random == 1) {
                this.day = 'red'
                this.c_text = "        ."
            }else if(random == 2) {
                this.day = 'yellow'
                this.c_text = " ,       ,  ,    .    !"

            }else if(random == 3) {
                this.day = 'green'
                this.c_text = " ,     ,    "
            }else if(random == 4) {
                this.day = 'blue'
                this.c_text = "       .        ,     .      ."
            }else if(random == 5) {
                this.day = 'purple'
                this.c_text = "      ."
            }else if(random == 6) {
                this.day = 'black'
                this.c_text = "   .   ."
            }
        })

    },

    watch: { // data         

    }
}
</script>